<template>
<div class="center">
     <div class="center_breadcrumb">
        <crumb :data="pageTitle"></crumb>
    </div>
    <div class="center_content">
        <div class="center_content_search aq-search">
            <div class="center_content_search_left">
               <span class="aq-title">{{title}}</span>
            </div>
             <div class="center_content_search_right">
                <el-button  plain>导出数据</el-button>
            </div>
        </div>
         <div class="table-container">
             <diagnoses :id="id"></diagnoses>
        </div> 
    </div>
 </div>
</template>

<script>
import Diagnoses from '../components/Diagnoses'

export default {
 name: 'AnalysisQuestionnaire',
 components: {Diagnoses},
 data() {
 return {
   pageTitle:['调查结果分析', '分析问卷'],
   title:this.$route.query.title || '',
   id:this.$route.query.id
 }
 },
 created(){},
 mounted(){},
 methods:{
  
 }
}
</script>

<style lang='scss'>
  @import '../../styles/center.scss'; 
  .aq-search{
       padding-bottom: 16px;
      .aq-title{
        line-height:38px;
        color:#333;
        font-size:14px;
    }
    .el-button{
       margin-right: 22px;
    }
  } 
 
</style>



